<template>
  <Container
    :muted="muted"
    :border="border"
    class="select-input"
    v-click-outside="hideDropdown"
  >
    <div class="select-input__body" @click="showDropdown = !showDropdown">
      <slot v-if="modelValue.value" name="selected" :item="{ modelValue }" />
      <span v-else class="select-input__placeholder">{{ placeholder }}</span>
      <Icon v-if="icon" :name="icon" class="select-input__body__append" />
      <slot name="append" class="select-input__body__append" />
    </div>
    <Container
      v-if="showDropdown"
      class="select-input__options"
    >
      <div
        v-for="item in filteredOptions"
        :key="item.value"
        class="select-input__options__item"
        @click="handleChange(item)"
      >
        <slot name="option" :option="item" />
      </div>
    </Container>
  </Container>
</template>

<script>
import { computed, ref } from 'vue'
import { useModel } from '@/utils/useModel'
import Container from '@/components/Container'
import Icon from '@/components/Icon/Icon'
import clickOutside from '@/directives/clickOutside'

export default {
  name: 'SelectInput',
  directives: {
    clickOutside
  },
  components: {
    Container, Icon
  },
  props: {
    placeholder: {
      type: String,
      default: ''
    },
    modelValue: {
      type: Object,
      default: () => ({})
    },
    icon: {
      type: String,
      default: ''
    },
    muted: {
      type: Boolean,
      default: false
    },
    border: {
      type: Boolean,
      default: false
    }
  },
  setup (props, { emit }) {
    const showDropdown = ref(false)
    const { value } = useModel(props, emit)
    const filteredOptions = computed(() => {
      return props.options?.filter(item => item.value !== props.modelValue?.value) || []
    })

    const hideDropdown = () => {
      showDropdown.value = false
    }
    const handleChange = (item) => {
      value.value = item
    }

    return {
      showDropdown,
      filteredOptions,
      hideDropdown,
      handleChange
    }
  }
}
</script>

<style lang="scss">
.select-input {
  position: relative;
  cursor: pointer;

  &__body {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 16px;

    &__append {
      margin-left: 11px;
    }
  }

  &__placeholder {
    color: #000000;
    opacity: 0.4;
  }

  &__options {
    position: absolute;
    left: 0;
    bottom: calc(100% + 3px);
    width: 100%;

    &__item {
      display: flex;
      justify-content: center;
      padding: 8px 19px;

      &:nth-of-type(2n - 1) {
        border-bottom: 1px solid $borderColor;
      }
    }
  }
}
</style>
