<template>
  <div class="pagination">
    <Button type="primary" class="pagination__page-next">
      Next Page <Icon name="arrow-right-full" />
    </Button>
    <div class="pagination__pages">
      <div
        v-for="item in pageSlots"
        :key="item"
        class="pagination__pages-item"
      >
        {{ item }}
      </div>
      <div class="pagination__pages-item pagination__page-divider">
        ...
      </div>
      <div class="pagination__pages-item pagination__page-last">
        {{ pagesCount }}
      </div>
    </div>
    <div class="pagination__goto">
      <Text variant="p" type="muted" mr="10px">Go to</Text>
      <Input
        placeholder="Page"
        type="number"
        hide-shadow
      />
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'
import Icon from '@/components/Icon/Icon'
import Button from '@/components/UIKit/Button'
import Text from '@/components/Text/Text'
import Input from '@/components/UIKit/Input'

export default {
  name: 'Pagination',
  components: { Input, Button, Icon, Text },
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      type: Number,
      default: 1
    },
    perPage: {
      type: Number,
      default: 100
    },
    totalCount: {
      type: Number,
      default: 100
    },
    slotsCount: {
      type: Number,
      default: 6
    }
  },
  setup (props) {
    const pagesCount = computed(() => Math.ceil(props.totalCount / props.perPage))
    const pageSlots = computed(() => {
      const arr = []
      for (let i = 0; i < props.slotsCount; i++) {
        arr.push(props.modelValue + i)
      }
      return arr
    })
    return {
      pagesCount,
      pageSlots
    }
  }
}
</script>

